:root {
  --cl-black: #0f1414;
  --cl-white: #ffffff;
  --cl-gray: #bfbfbf;
  --cl-red: #bc2e37;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: var(--cl-black);
  color: var(--cl-white);
  margin: 0;
  padding: 0;
}

main {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}
@media only screen and (min-wid1th: 1399px) {
  main {
    max-width: 1100px;
  }
}
@media only screen and (max-width: 1179px) {
  main {
    max-width: 1000px;
  }
}
@media only screen and (max-width: 999px) {
  main {
    max-width: 900px;
  }
}

section {
  min-height: 100vh;
}
@media only screen and (max-width: 1179px) {
  section {
    padding: 10vh 4vh;
  }
}

.color-accent {
  color: var(--cl-red);
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}

@font-face {
  font-family: "OpenSans";
  src: url("../app/assets/fonts/OpenSans-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "OpenSans";
  src: url("../app/assets/fonts/OpenSans-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "OpenSans";
  src: url("../app/assets/fonts/OpenSans-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
body {
  font-family: "OpenSans", sans-serif;
  font-weight: 300;
  font-size: 18px;
  color: var(--cl-white);
  line-height: 1.8;
}
@media only screen and (max-width: 999px) {
  body {
    font-size: 16px;
    line-height: 1.4;
  }
}
@media only screen and (max-width: 899px) {
  body {
    font-size: 14px;
  }
}
@media only screen and (max-width: 759px) {
  body {
    font-size: 18px;
    line-height: 1.6;
  }
}

h1,
h2,
h3 {
  margin: 0;
}

a {
  text-decoration: none;
  text-transform: uppercase;
  color: var(--cl-white);
  font-weight: 400;
  display: inline-block;
  padding-bottom: 6px;
  border-bottom: 1px solid var(--cl-red);
}

.h1 {
  font-size: 42px;
  text-align: center;
  margin-bottom: 4rem;
}
@media only screen and (max-width: 999px) {
  .h1 {
    font-size: 36px;
  }
}
@media only screen and (max-width: 899px) {
  .h1 {
    font-size: 24px;
  }
}
@media only screen and (max-width: 759px) {
  .h1 {
    font-size: 32px;
  }
}

.h2 {
  font-size: 24px;
}
@media only screen and (max-width: 999px) {
  .h2 {
    font-size: 20px;
  }
}
@media only screen and (max-width: 899px) {
  .h2 {
    font-size: 18px;
  }
}
@media only screen and (max-width: 759px) {
  .h2 {
    font-size: 24px;
  }
}

.h3 {
  font-size: 18px;
  color: var(--cl-white);
}
@media only screen and (max-width: 999px) {
  .h3 {
    font-size: 16px;
  }
}
@media only screen and (max-width: 899px) {
  .h3 {
    font-size: 14px;
  }
}
@media only screen and (max-width: 759px) {
  .h3 {
    font-size: 18px;
  }
}

.text-light {
  font-weight: 300;
}

.text-bold {
  font-weight: 500;
}

.text-feather {
  font-weight: 300;
  color: var(--cl-gray);
}

.inset {
  font-size: 16px;
}
@media only screen and (max-width: 899px) {
  .inset {
    font-size: 14px;
  }
}
@media only screen and (max-width: 759px) {
  .inset {
    font-size: 16px;
  }
}

.uppercase {
  text-transform: uppercase;
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 24px;
  width: 100%;
}
@media only screen and (max-width: 999px) {
  .grid {
    gap: 12px;
  }
}

.component_1 {
  height: 600px;
  align-items: start;
}
.component_1 > * {
  height: 100%;
  max-width: 100%;
}
@media only screen and (max-width: 620px) {
  .component_1 > * {
    width: 100%;
  }
}
@media only screen and (max-width: 759px) {
  .component_1 > *:last-child {
    grid-column: span 2;
  }
}
@media only screen and (max-width: 620px) {
  .component_1 > *:last-child {
    grid-column: span 1;
  }
}
@media only screen and (min-wid1th: 1399px) {
  .component_1 {
    min-height: max-content;
  }
}
@media only screen and (max-width: 1179px) {
  .component_1 {
    min-height: 100vh;
    height: 100%;
  }
}
@media only screen and (max-width: 759px) {
  .component_1 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    width: 80%;
    text-align: center;
  }
}
@media only screen and (max-width: 620px) {
  .component_1 {
    grid-template-columns: 1fr;
    width: 100%;
    padding: 0 20px;
  }
}

.flex-col {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 24px;
}
@media only screen and (max-width: 999px) {
  .flex-col {
    gap: 12px;
  }
}
.flex-col > * {
  max-width: 100%;
  height: 100%;
}

@media only screen and (max-width: 759px) {
  .component_2 {
    grid-template-columns: 1fr;
    justify-content: center;
  }
}
@media only screen and (max-width: 759px) {
  .component_2 > * {
    display: flex;
    flex-direction: column;
    text-align: center;
  }
}
@media only screen and (max-width: 759px) {
  .component_2 > * img {
    width: 70%;
    margin: 0 auto 24px;
  }
}
@media only screen and (max-width: 620px) {
  .component_2 > * img {
    width: 100%;
  }
}
@media only screen and (max-width: 759px) {
  .component_2 > * p {
    max-width: 71ch;
  }
}
@media only screen and (max-width: 759px) {
  .component_2 > * a {
    width: max-content;
    margin: 0 auto 48px;
  }
}

.img-underlined {
  border-bottom: 4px solid var(--cl-red);
  margin-bottom: 2rem;
  max-width: 100%;
}

#modal {
  display: none;
  position: fixed;
  z-index: 1;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.9);
}

#close {
  position: absolute;
  top: 5%;
  right: 5%;
  color: #f1f1f1;
  font-size: 40px;
  font-weight: bold;
  transition: 0.3s;
}
#close:hover, #close:focus {
  color: #bbb;
  text-decoration: none;
  cursor: pointer;
}

.modal-img {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: calc(100vh - 100px);
  padding-bottom: 100px;
}
.modal-img > img {
  height: 100%;
  max-width: 100%;
  width: auto;
}

.img {
  transition: opacity 0.3s ease-in-out;
}
.img:hover {
  opacity: 0.4;
  cursor: pointer;
}

#modal-img-1 {
  display: none;
}

#modal-img-2 {
  display: none;
}

#modal-img-3 {
  display: none;
}

#modal-img-4 {
  display: none;
}

#modal-img-5 {
  display: none;
}

#modal-img-6 {
  display: none;
}

@keyframes slideDown {
  0% {
    opacity: 0;
    transform: translateY(-100px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
#img-1 {
  opacity: 0;
  animation: slideDown 0.6s ease-out forwards;
}

#img-2 {
  opacity: 0;
  animation: slideDown 0.6s 0.3s ease-out forwards;
}

#img-3 {
  opacity: 0;
  animation: slideDown 0.6s 0.6s ease-out forwards;
}

.component_1 > div:last-of-type {
  opacity: 0;
  animation: slideDown 0.6s 0.9s ease-out forwards;
}

.animate {
  opacity: 0;
  animation: slideDown 0.6s ease-out forwards;
}